import React,{Component} from 'react';
import style from '../public/css/search.scss';
import { PropTypes } from 'prop-types';
import {connect} from 'react-redux';


class Search extends Component{
    render () {
        return (
            <div onClick={this.searchPage.bind(this)} className={`${style.search} ${this.props.search ? style.searchPage : ''}`}>
                <input ref={(input) => this.input = input} onInput={this.searchInput.bind(this)}  />
                <span  onClick={this.context.router.history.goBack}>取消</span>
                <div className={style.text}>
                    <span></span>
                    <i>搜索</i>
                    <b></b>
                </div>
            </div>
        )
    }
    searchPage  ()  {
        if (!this.props.search) {
            this.input.focus();
            this.context.router.history.push('/search');
        }
    }
    searchInput () {
    }
}
Search.contextTypes = {
    router: PropTypes.object,
};
Search.propTypes = {
    search: PropTypes.number.isRequired
};

const mapStateToProps = (state) => ({
    search: state.search
});

export default connect(mapStateToProps)(Search)