import {createStore, combineReducers, applyMiddleware} from 'redux';
import thunk from 'redux-thunk'

function posts(posts = [], action) {
    switch (action.type) {
        case 'add':
            return posts
        case 'init':
            return posts
        default:
            return posts
    }
}
function search(search = 0, action) {
    switch (action.type) {
        case 'search':
            return action.state
        default:
            return search
    }
}

const store = createStore(combineReducers({posts, search}), applyMiddleware(thunk))

export default store